"use client";

import React from 'react';

const PokeCard = ({ pokemon }) => {
  const router = useRouter();

  const handleCardClick = () => {
    router.push({
      pathname: `/pokemon/${pokemon.id}`,
      query: { pokemon: JSON.stringify(pokemon) },
    });
  };

  return (
    <div className="poke-card" onClick={handleCardClick}>
      <img src={pokemon.image} alt={pokemon.name} className="poke-image" />
      <h3 className="poke-name">{pokemon.name}</h3>
      <p className="poke-id">#{pokemon.pokedexId}</p>
      <div className="poke-types">
        {pokemon.types.map((type) => (
          <img
            key={type.id}
            src={type.image}
            alt={type.name}
            className="poke-type-icon"
            title={type.name}
          />
        ))}
      </div>
    </div>
  );
};

export default PokeCard;
